<script>
import { useToast, FwbButton, FwbRating } from "flowbite-vue";

import { ref, onMounted, getCurrentInstance } from "vue";
import LoadSkeletonHome from ".././widgets/LoadSkeletonHome.vue";
import ToolsWidget from ".././widgets/ToolsWidget.vue";
import { Swiper, SwiperSlide } from "swiper/vue";
import {
  Navigation,
  Autoplay,
  Pagination,
  Scrollbar,
  A11y,
} from "swiper/modules";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";
export default {
  components: {
    LoadSkeletonHome,
    ToolsWidget,
    Swiper,
    SwiperSlide,
    FwbButton,
    FwbRating,
  },
  setup() {
    const data = ref(null);
    const error = ref(null);
    const loading = ref(true);
    const toast = useToast();
    const { proxy } = getCurrentInstance();
    const onSwiper = () => {
      // console.log(swiper);
    };
    const onSlideChange = () => {
      // console.log("slide change");
    };

    function fetchData() {
      let api = `${proxy.COMMON.apiUrl}/v1/api/video/jojo/index?size=12`;
      fetch(api, {
        method: "post",
        headers: {
          "content-type": "application/json",
          Authorization: localStorage.getItem("Authorization"),
        },
      })
        .then((res) => {
          if (!res.ok) {
            const error = new Error(res.statusText);
            error.json = res.json();
            throw error;
          }
          return res.json();
        })
        .then((json) => {
          if (json.code == 200) {
            data.value = json.data;
          } else if (json.code == 402) {
            toast.add({
              type: "success",
              time: 3000,
              text: "刷新token成功!",
            });
            localStorage.setItem("Authorization", json.data);
          } else if (json.code == 403) {
            toast.add({
              type: "warning",
              time: 3000,
              text: "登录失效,请重新登录!",
            });
            proxy.COMMON.loginOut();
          } else {
            toast.add({
              type: "success",
              time: 3000,
              text: json.msg,
            });
          }
        })
        .catch((err) => {
          error.value = err;
          if (err.json) {
            return err.json.then((json) => {
              error.value.message = json.message;
              toast.add({
                type: "success",
                time: 3000,
                text: error.value.message,
              });
            });
          }
        })
        .then(() => {
          loading.value = false;
        });
    }
    onMounted(() => {
      document.title = proxy.COMMON.title;
      fetchData();
    });
    return {
      data,
      loading,
      toast,
      onSwiper,
      onSlideChange,
      modules: [Navigation, Pagination, Scrollbar, A11y, Autoplay],
    };
  },
  methods: {},
};
</script>

